import React, { useRef, useEffect, useState } from 'react';
import { Button } from '../Interface/Button';
import styles from './Editor.module.css';

export function TextEditor({ content, onChange, onImageSelect }) {
  const editorRef = useRef(null);
  const fileInputRef = useRef(null);
  const charCount = content.length;
  const [selectedImage, setSelectedImage] = useState(null);

  useEffect(() => {
    // Keep the editor content in sync with the content prop
    if (editorRef.current && editorRef.current.textContent !== content) {
      editorRef.current.textContent = content;
    }
  }, [content]);

  const handleInput = (e) => {
    const newContent = e.target.textContent;
    onChange({ target: { value: newContent } });
  };

  const handlePaste = (e) => {
    e.preventDefault();
    const text = e.clipboardData.getData('text/plain');
    document.execCommand('insertText', false, text);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      document.execCommand('insertLineBreak');
    }
  };

  const handleImageClick = () => {
    fileInputRef.current?.click();
  };

  const handleImageSelect = (e) => {
    const file = e.target.files?.[0];
    if (file) {
      setSelectedImage(file);
      onImageSelect?.(file);
    }
  };

  return (
    <div className={styles.editor}>
      <div
        ref={editorRef}
        className={styles.canvas}
        contentEditable
        onInput={handleInput}
        onPaste={handlePaste}
        onKeyDown={handleKeyDown}
        role="textbox"
        aria-multiline="true"
        aria-label="Post content"
        data-placeholder="What's on your mind?"
      />

      <div className={styles.tools}>
        <div className={styles.site}>shaunandrews.com</div>
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleImageSelect}
          accept="image/jpeg,image/png,image/gif,image/webp"
          style={{ display: 'none' }}
        />
        <Button variant="default" onClick={handleImageClick}>
          {selectedImage ? selectedImage.name : 'Select image'}
        </Button>
        <div className={styles.count}>
          {charCount} characters
        </div>
      </div>
    </div>
  );
} 